'use strict';

var assign = require('object-assign');
var Reflux = require('reflux');
var MetaActions = require('../actions/pageMetaActions');
var api = require('../api');
var getRouter = require('../router.jsx').get;

var actions = Reflux.createActions({
  setInternationalBounds: {},
  changeBounds: {children: ['user']},
  changeCenter: {children: ['user']},
  changeZoom: {children: ['user']},
  resetBounds: {},
  changeBasemap: {},  // Only for country view
  refreshFromURL: {},
  changeThematic: {},
  loadThematicLegend: {},
});

actions.loadThematicLegend = Reflux.createAction({ asyncResult: true });
actions.loadThematicLegend.listenAndPromise(api.getThematicLayerLegend);

actions.changeBasemap.listen(function(basemapId) {
  var query = getRouter().getCurrentQuery();
  MetaActions.transitionTo('country', {}, assign({}, query, {bm: basemapId}));
});

actions.changeThematic.listen(function(thematicId) {
  var query = getRouter().getCurrentQuery();
  if (thematicId === query.thm) {
    MetaActions.transitionTo('country', {}, assign({}, query, {thm: undefined}));
  } else {
    MetaActions.transitionTo('country', {}, assign({}, query, {thm: thematicId}));
  }
});

module.exports = actions;
