'use strict';

var numeral = require('numeral');
var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;
var MapViewActions = require('../actions/mapViewActions');
var MetaActions = require('../actions/pageMetaActions');
var CountryActions = require('../actions/countryActions');
var CountrySummaryStore = require('../stores/countrySummaryStore');
var EsriTileLayer = require('./map/esriTileLayer.jsx');
var PointLayer = require('./map/pointLayer.jsx');
var Popup = require('./map/popup.jsx');
var Header = require('./header.jsx');


var CountryTotal = React.createClass({displayName: "CountryTotal",
  render: function() {
    var formatter = this.props.unit === 'USD' ? '$0,0' : '0,0';
    var amount = numeral(this.props.amount).format(formatter);
    return (
      React.createElement("tr", {className: "total"}, 
        React.createElement("th", null, this.props.title), 
        React.createElement("td", null, 
          React.createElement("span", {className: "amount"}, amount)
        )
      )
    );
  },
});


var ListedCountry = React.createClass({displayName: "ListedCountry",
  render: function() {
    return (
      React.createElement("li", null, 
        React.createElement(Link, {to: "country", params: {countryId: this.props.id}}, 
          this.props.name
        )
      )
    );
  }
});


var International = React.createClass({displayName: "International",

  mixins: [Reflux.connect(CountrySummaryStore, 'countries')],

  getInitialState: function() {
    return {
      countryListOpen: false,
    };
  },

  componentWillMount: function() {
    CountryActions.load.triggerPromise()
      .then(MapViewActions.resetBounds);
  },

  componentDidUpdate: function() {
    var max = -Infinity, min = Infinity;
    var featureArray = this.state.countries.features;
    var length = featureArray.length;
    for (var i = 0; i < length; i++) {
      var projects = featureArray[i].properties.totals.projects.amount;
      if(projects > max) {
        max = projects;
      }
      if(projects < min) {
        min = projects;
      }
    }
    if(this.state.projectMax != max || this.state.projectMin != min) {
      this.setState({ projectMax: max, projectMin: min })
    }
  },

  pin: function(featureData, latlng) {
    var size; // size for divIcon
    var min_size = 10;
    var max_size = 64;
    var range_size = max_size - min_size;

    var min = this.state.projectMin;
    var max = this.state.projectMax;
    var range = max - min;

    var amount = featureData.properties.totals.projects.amount;
    size = (amount - min) / range * range_size + min_size;

    if (size < min_size) {
      console.error('Number of projects less than calculated minimum.');
      size = min_size;
    }
    if (size > max_size) {
      console.error('Number of projects more than calculated maximum.');
      size = max_size;
    }

    return L.marker(latlng, {
      icon: L.divIcon({
        className: 'country-location-marker',
        iconSize: [size, size],
        iconAnchor: [size/2, size/2],
      }),
      title: featureData.properties.name,
      alt: 'Clickable map marker for ' + featureData.properties.name,
      riseOnHover: true
    });
  },

  popup: function(country) {
    var d = country.properties;
    var countryPage;
    if(country.id === 'PH') {
      countryPage = "http://www.adb.org/countries/philippines/main";
    } else if(country.id === 'IN') {
      countryPage = "http://www.adb.org/countries/india/main";
    }
    return (
      React.createElement(Popup, {className: "country-popup"}, 
        React.createElement(Popup.Header, null, 
          React.createElement("h3", {className: "panel-title totals"}, 
              React.createElement("strong", null, d.name)
          )
        ), 

        React.createElement("div", {className: "panel-body totals"}, 
          React.createElement("table", {className: "table table-condensed country-overview-data"}, 
            React.createElement("tbody", null, 
              React.createElement(CountryTotal, React.__spread({title: "Total Geocoded Projects"},  d.totals.projects)), 
              React.createElement(CountryTotal, React.__spread({title: "Total Geocoded Locations"},  d.totals.locations))
            )
          ), 
          React.createElement("p", {className: "clearfix country-footer"}, 
            React.createElement(Link, {to: "country", className: "pull-right main-target", query: {c: [country.id]}}, "Go to country map"), 
            React.createElement("a", {href: countryPage, className: "pull-left not-ready"}, "Go to country overview")
          )
        )
      )
    );
  },

  dblclick: function(feature, layer) {
    MetaActions.transitionTo('country', {}, {c: [feature.id]});
  },

  toggleCountryList: function(e) {
    var open = !this.state.countryListOpen;  // toggles by default
    if (e !== undefined) {
      e.preventDefault();
      var toggleMode = e.target.getAttribute('data-toggle');
      if (toggleMode === 'open') {
        open = true;
      } else if (toggleMode === 'closed') {
        open = false;
      }
    }
    this.setState({countryListOpen: open});
  },

  render: function() {
    var countries = this.state.countries.features.sort(function(a, b) {
      return a.properties.name < b.properties.name ? -1 : 1;
    });

    return (
      React.createElement("div", null, 
        React.createElement(EsriTileLayer, {theme: "Gray", getMap: this.props.getMap}), 
        React.createElement(Header, null, 
          React.createElement(Header.Main, null, 
            React.createElement(Header.Item, null, 
              React.createElement("span", {className: "navbar-brand"}, "Sovereign Projects")
            )
          )
        ), 
        React.createElement(PointLayer, {
          getMap: this.props.getMap, 
          geojson: this.state.countries, 
          pin: this.pin, 
          popup: this.popup, 
          popupMaxWidth: 320, 
          projectMax: this.state.projectMax, //set projectMax and projectMin as props to ensure page re-render after final values are calculated
          projectMin: this.state.projectMin, 
          dblclick: this.dblclick})
      )
    );
  }

});


module.exports = International;
