'use strict';

var Reflux = require('reflux');
var SelectedCountryStore = require('./selectedCountryStore');


var LocationsStore = Reflux.createStore({

  init: function() {
    this.data = this.getInitialState();
    this.listenTo(SelectedCountryStore, this.updateCountries);
  },

  updateCountries: function(updatedCountries) {
    this.data = updatedCountries.features
      .reduce(function(locations, country) {
        if (country.properties.locations !== undefined) {
          return locations.concat(country.properties.locations);
        } else {
          return locations;
        }
      }, []);
    this.emit();
  },

  emit: function() {
    this.trigger(this.data);
  },

  getInitialState: function() {
    return [];
  },

});


module.exports = LocationsStore;
