'use strict';

var L = window.L;  // explicitly bring into scope to not be evil
var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;
var Header = require('./header.jsx');
var Popup = require('./map/popup.jsx');
var EsriTileLayer = require('./map/esriTileLayer.jsx');
var PointLayer = require('./map/pointLayer.jsx');
var locationPin = require('./locationPin');
var titleCase = require('to-title-case');
var ProjectActions = require('../actions/projectActions');
var MapViewActions = require('../actions/mapViewActions');
var CountryStore = require('../stores/countryStore');
var ProjectsStore = require('../stores/projectsStore');
var numeral = require('numeral');


function projectFeature(country, projectId) {
  var locations = country.properties.locations.filter(function(location) {
    return location.properties.id === projectId;
  });
  return {
    'type': 'FeatureCollection',
    'features': locations,
  };
}

var PopupItem = React.createClass({displayName: "PopupItem",
  render: function() {
    return (
      React.createElement("tr", {className: "total"}, 
        React.createElement("th", null, this.props.title), 
        React.createElement("td", null, 
          React.createElement("span", null, this.props.value)
        )
      )
    );
  },
});


var Project = React.createClass({displayName: "Project",

  propTypes: {
    id: React.PropTypes.number,
    name: React.PropTypes.string,
    totalCommitments: React.PropTypes.number,
  },

  mixins: [
    Reflux.connect(ProjectsStore, 'projects'),
    Reflux.connect(CountryStore, 'countries'),
  ],

  componentWillMount: function() {
    // load country and project data
    ProjectActions.loadProject.triggerPromise(this.props.params.projectId)
      .then(this.projectDidLoad);
  },

  componentWillReceiveProps: function(nextProps) {
    if (nextProps.params.projectId !== this.props.params.projectId) {
      // user navigated from one country directly to another's url
      delete this.country;
      delete this.project;
      ProjectActions.loadProject.triggerPromise(nextProps.params.projectId)
        .then(this.projectDidLoad);
    }
  },

  projectDidLoad: function(rawCountryProject) {
    this.country = CountryStore.utils.countryToFeature(rawCountryProject);

    this.project = projectFeature(this.country, this.props.params.projectId);

    var points = this.country.properties.locations
      .filter(function(location) {
        return (location.properties.id === this.props.params.projectId);
      }, this)
      .map(function(location) {
        return location.geometry.coordinates.slice().reverse();  // reversed because our geojson is backward
      });

    MapViewActions.changeBounds(L.latLngBounds(points), {});
  },

  locationPopup: function(locationObj) {
    var sectors = locationObj.properties.sectors.reduce(function(prev, curr) {
      return prev + ', ' + curr;
    });
    var formatter = '$0,0';
    //this.highlightProject(project.id);
    return (
      React.createElement(Popup, {className: "general-popup"}, 
        React.createElement(Popup.Header, null, 
          React.createElement("h3", {className: "panel-title"}, locationObj.properties.locationName, ", ", locationObj.properties.countryName)
        ), 
        React.createElement("div", {className: "panel-body totals"}, 
          React.createElement("h4", null, titleCase(locationObj.properties.title)), 
          React.createElement("p", {className: "country-summary"}, " ", locationObj.properties.info, " "), 
          React.createElement("table", {className: "table table-condensed"}, 
            React.createElement("tbody", null, 
              React.createElement(PopupItem, {title: "Project ID", value: locationObj.properties.id}), 
              React.createElement(PopupItem, {title: "Sector(s)", value: sectors}), 
              React.createElement(PopupItem, {title: "Approval Date", value: locationObj.properties.approvalDate || 'unknown'}), 
              React.createElement(PopupItem, {title: "Commitment Amount", value: numeral(locationObj.properties.commitmentAmount).format(formatter)}), 
              React.createElement(PopupItem, {title: "Disbursement Amount", value: numeral(locationObj.properties.disbursementAmount).format(formatter)}), 
              React.createElement(PopupItem, {title: "Closing Date", value: locationObj.properties.closingDate || 'unknown'})
            )
          )
        )
      )
    );
  },

  render: function() {
    var countryHasLoaded = Boolean(this.country);
    var projectTitle;
    var locations = [];

    if(countryHasLoaded) {
      var totalLocations = this.country.properties.locations.length;
      for(var i=0; i<totalLocations; i++) {
        locations.push(this.country.properties.locations[i].properties.locationName);
      }
      locations = locations.slice(0, 3);
      if(totalLocations < 4) {
        projectTitle = locations.join(', ');
      }
      else {
        projectTitle = locations.join(', ') + ' + ' + (totalLocations-3) + ' other locations';
      }
    }

    var LinkToParentCountry = (
      React.createElement("a", {className: "country"}, 
        ""
      )
    );

    if(countryHasLoaded && this.props.params.countryId) {

          //countryNames.join(', ') + '  ' + locationsCount + ' Project Locations'}
      var LinkToParentCountry = (
        React.createElement(Link, {to: "country", query: {c: [this.props.params.countryId]}, className: "back"}, 
          "Back to ", this.country.properties.name, " projects"
        )
      );
    }
    var projectHeader = countryHasLoaded && (
      React.createElement("a", {className: "country"}, 
        projectTitle
      )
    );

    var header = countryHasLoaded && (
      React.createElement(Header, null, 
        React.createElement(Header.Main, null, 
          React.createElement(Header.Item, null, 
            LinkToParentCountry
          ), 
          React.createElement(Header.Item, null, 
            projectHeader
          )
        )
      )
    );

    return (
      React.createElement("div", null, 
        React.createElement(EsriTileLayer, {theme: "Streets", getMap: this.props.getMap}), 
        header, 
        React.createElement(PointLayer, {
          key: 'la-la-la', 
          getMap: this.props.getMap, 
          pin: locationPin, 
          popup: this.locationPopup, 
          geojson: this.project})
      )
    );
  }

});


module.exports = Project;
