'use strict';

var assign = require('object-assign');
var Reflux = require('reflux');
var MapViewActions = require('../actions/mapViewActions');
var getRouter = require('../router.jsx').get;

function clearSelected(thematicLayer) {
  return assign({}, thematicLayer, {
    selected: false
  });
}

function isTheLayerAlreadySelected(thematicLayers, thematicId) {
  return !thematicLayers.some(function(thematicLayer) {
    if (thematicLayer.id === thematicId && thematicLayer.selected === false) {
      return true;
    }
  });
}

function selectThematicLayer(thematicLayers, thematicId) {
  return thematicLayers.map(function(thematicLayer) {
    if (thematicLayer.id === thematicId) {
      return assign({}, thematicLayer, {
        selected: true
      });
    } else {
      return thematicLayer;
    }
  });
}

var thematicLayersStore = Reflux.createStore({


  listenables: MapViewActions,

  init: function() {
    this.data = [{
      id: 'adm1_popn_afg',
      url: 'http://gis.developmentgateway.org/arcgis/rest/services/ADBPublic/adm1_popn_afg/MapServer',
      layer: 'adm1-asiawide-AFG',
      description: 'The population of the provinces of Afghanistan, based on WorldPop estimated-2015 data and Natural Earth boundaries.',
      name: 'Population of Provinces',
      country: 'AF',
      selected: false
    }, {
      id: 'adm1_popn_afg_pphdensity',
      url: 'http://gis.developmentgateway.org/arcgis/rest/services/ADBPublic/adm1_popn_afg_pphdensity/MapServer',
      layer: 'adm1-asiawide-AFG-density',
      description: 'The population density in Person per Hectare, of the provinces of Afghanistan, based on WorldPop estimated-2015 data and Natural Earth boundaries.',
      name: 'Population Density of Provinces',
      country: 'AF',
      selected: false
    },{
      id: 'PHPop',
      url: 'http://gis.developmentgateway.org/arcgis/rest/services/ADBPublic/adm1_popn_phl/MapServer',
      layer: 'adm1-asiawide-PHL',
      description: 'The population of the regions of the Philippines, based on WorldPop estimated-2015 data and Natural Earth boundaries.',
      name: 'Population of Regions',
      country: 'PH',
      selected: false
    }, {
      id: 'INPop',
      url: 'http://gis.developmentgateway.org/arcgis/rest/services/ADBPublic/adm1_popn_ind/MapServer',
      description: 'The population of the states of India, based on WorldPop estimated-2015 data and Natural Earth boundaries.',
      layer: 'adm1-asiawide-IND',
      country: 'IN',
      name: 'Population of States',
      selected: false
    }, {
      id: 'adm1_popn_tha',
      url: 'http://gis.developmentgateway.org/arcgis/rest/services/ADBPublic/adm1_popn_tha/MapServer',
      description: 'The population of the provinces of Thailand, based on WorldPop estimated-2015 data and Natural Earth boundaries.',
      layer: 'adm1-asiawide-utf8-THA',
      country: 'TH',
      name: 'Population of Provinces',
      selected: false
    }, {
      id: 'adm1_popn_lka',
      url: 'http://gis.developmentgateway.org/arcgis/rest/services/ADBPublic/adm1_popn_lka/MapServer',
      description: 'The population density in Person per Hectare, of the provinces of Sri Lanka, based on WorldPop estimated-2015 data and Natural Earth boundaries.',
      layer: 'adm1-asiawide-utf8-lka',
      country: 'LK',
      name: 'Population Density of Provinces',
      selected: false
    },
    {
      id: 'dhs_infant_lk',
      url: 'http://gis.developmentgateway.org/arcgis/rest/services/ADBPublic/dhs_infant_lk/MapServer',
      description: 'Infant Mortality in Sri Lanca based on US Government 2010 Demographics & Health Survey. Probability of dying before the first birthday (in the period 1-60 months preceding the survey) per 1,000 live births',
      layer: 'dhs_infant_lk',
      country: 'LK',
      name: 'Infant Mortality (# per 1,000)',
      selected: false
    },, ];
    this.listenTo(MapViewActions.refreshFromURL, this.onRefreshFromURL);
  },

  onChangeThematic: function(thematicId) {
    var wasSelected = isTheLayerAlreadySelected(this.data, thematicId);
    var updated = this.data.map(clearSelected);
    if (!wasSelected) {
      updated = selectThematicLayer(updated, thematicId);
    }
    this.data = updated;

    /* emit will occur when URL updates */
  },


  onRefreshFromURL: function() {
    var query = getRouter().getCurrentQuery();
    /* URL query thm item toggling is set up in the MapViewActions */
    this.data = selectThematicLayer(this.data, query.thm);

    this.emit();
  },


  emit: function() {
    this.trigger(this.data);
  },

  getInitialState: function() {
    return this.data;
  },

});


module.exports = thematicLayersStore;
