'use strict';

var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;
var MapViewActions = require('../actions/mapViewActions');


var Legend = React.createClass({displayName: "Legend",
  getInitialState: function() {
    return {init: true, expanded: false, filterClicked: false};
  },

  expandMe: function(e) {
    if (e) { e.preventDefault(); }  // if this is coming from a click event
    if (this.state.expanded) {
      this.setState({expanded: false});
    }
    else {
      this.setState({expanded: true});
    }
  },

  componentWillMount: function() {
    // load country and project data
    if (this.props.thematicLayers) {
      MapViewActions.loadThematicLegend.triggerPromise(this.props.thematicLayers)
        .then(this.legendDidLoad);
    }
  },

  componentWillReceiveProps: function(nextProps) {
    if (nextProps.thematicLayers !== this.props.thematicLayers && nextProps.thematicLayers !== null) {
      this.setState({legendData: undefined});
      MapViewActions.loadThematicLegend.triggerPromise(nextProps.thematicLayers)
        .then(this.legendDidLoad);

    } else if(nextProps.thematicLayers !== this.props.thematicLayers) {
      this.setState({legendData: undefined});
    }
  },
  legendDidLoad: function(rawESRILegend) {
    if (rawESRILegend) {
      this.setState({
        legendData: rawESRILegend
      });
    }
  },

  render: function() {
    var allItems, title = null;

    if (this.state.legendData && this.props.thematicLayers) {
      allItems = this.state.legendData.layers[0].legend;
      title = this.props.thematicLayers.name;
      /*TODO(tdk): change title to use name from inside layers[0] rather than name from Store */

      return (
        React.createElement("div", {className: 'legend ' + (this.state.expanded ? 'expanded ' : '')}, 
          React.createElement("div", {className: "container-fluid"}, 
            this.props.children
          ), 

          React.createElement("div", {className: "legend-header text-center"}, 
            React.createElement("a", {href: "#", onClick: this.expandMe}, "Legend")
          ), 

          React.createElement("div", {className: "legend-content"}, 
            React.createElement(Legend.Main, {legendTitle: title, legendItems: allItems})

          )/* End Legend Content */
        )
      );
    } else {
      return (
        React.createElement("div", {className: 'legend hidden'})
      );
    }
  },
});

var makeLegendItem = function (legendItem) {
  return (
    React.createElement(Legend.Item, {legend: legendItem, key: legendItem.url})
  );
};


Legend.Main = React.createClass({displayName: "Main",
  render: function() {
    var theseLegendItems = null;
    if (this.props.legendItems && this.props.legendItems.length > 0) {
      theseLegendItems = this.props.legendItems.map(function(legendItem) {
        return makeLegendItem(legendItem);
      });
    }

    return (
      React.createElement("div", null, React.createElement("h3", null, this.props.legendTitle), 
        this.props.children, " ", theseLegendItems
      )
    );
  },
});


Legend.Item = React.createClass({displayName: "Item",
  render: function() {
    if (this.props.legend && this.props.legend.label && this.props.legend.imageData) {
      return (
        React.createElement("div", {className: (this.props.className ? this.props.className : '')}, 
          React.createElement("img", {src: 'data:' + this.props.legend.contentType + ';base64,' + this.props.legend.imageData, className: "color-block legend-sample"}), 
          React.createElement("span", {className: "legend-label"}, " ", this.props.legend.label), 
          this.props.children
        )
      );
    } else {
      return (
        React.createElement("div", null, "empty")
      );
    }
  },
});


module.exports = Legend;
