'use strict';

var Reflux = require('reflux');
var FilteredLocationsStore = require('./filteredLocationsStore');
var FilterActions = require('../actions/filterActions');


var SystemErrorStore = Reflux.createStore({
  init: function() {
    this.listenTo(FilteredLocationsStore, this.onStoreChange);
    this.listenTo(FilterActions.setFilter, this.onSetFilter);
  },

  onStoreChange: function(store) {
    if(store.length === 0) {
      this.emit({closed: false});
    }
  },

  onSetFilter: function(filterData) {
    this.emit({filterClicked: true});
  },

  emit: function(data) {
    this.trigger(data);
  }

});

module.exports = SystemErrorStore;
