'use strict';

var assign = require('object-assign');
var Reflux = require('reflux');
var FiltersViewActions = require('../actions/filtersViewActions');
var FilterStore = require('./filterStore');


module.exports = Reflux.createStore({
  listenables: FiltersViewActions,

  init: function() {
    this.listenTo(FilterStore, this.filterUpdate);
  },

  onOpen: function(filterViewId) {
    this.update({currentlyOpen: filterViewId});
  },

  onClose: function() {
    this.update({currentlyOpen: null});
  },

  onMinimize: function() {
    this.update({isMinimized: true});
  },

  onUnminimize: function() {
    this.update({isMinimized: false});
  },

  filterUpdate: function(newFilter) {
    var activated = Object.keys(newFilter)
      .filter(function(k) { return newFilter[k].length > 0; });
    this.update({applied: activated});
  },

  update: function(spec) {
    this.state = assign({}, this.state, spec);
    this.trigger(this.state);
  },

  getInitialState: function() {
    return this.state || (this.state = {
      currentlyOpen: null,
      isMinimized: false,
      applied: [],
    });
  }
});
