'use strict';

var React = require('react');
var Map = require('./map/map.jsx');
var RouteHandler = require('react-router').RouteHandler;
var MetaActions = require('../actions/pageMetaActions');

module.exports  = React.createClass({displayName: "exports",
  render: function() {
    return (
      React.createElement(Map, {
        scrollWheelZoom: false, 
        attributionControl: false, 
        className: "main-map"}, 
        React.createElement(RouteHandler, React.__spread({},  this.props))
      )
    );
  },
  componentWillMount: function() {

    // var grantParent = React.findDOMNode(this) && React.findDOMNode(this).parentNode.parentNode;
    var parent = document.getElementById('asdb-map-app-embed');

    /* Anticipating the structure of
     * <div class="asdb-map" data-sector-id="Energy">
     *  <div class="asdb-map-app-embed">
     *    <this root element>
     *  </div>
     * </div>
     */

    if (parent && parent.getAttribute('data-countryId')) {
      var inputCountryId = parent.getAttribute('data-countryId');
      MetaActions.transitionTo('country', {}, {c: [inputCountryId]});

    } else if (parent && parent.getAttribute('data-sectorId')) {
      var inputSectorId = parent.getAttribute('data-sectorId');
      MetaActions.transitionTo('countrySector', {sectorId:inputSectorId},  {});

    } else if (parent && parent.getAttribute('data-projectId')) {
      var inputProjectId = parent.getAttribute('data-projectId');
      MetaActions.transitionTo('projectOnly', {projectId: inputProjectId}, {});

    } 
  }
});
