'use strict';


// circular import handling: indirection via getter function that is called later
module.exports = {
  get: function() {
    return router;
  }
};


var React = require('react');
var Router = require('react-router');
var Route = require('react-router').Route;
var DefaultRoute = require('react-router').DefaultRoute;


var Root = require('./components/root.jsx');
var International = require('./components/international.jsx');
var Sector = require('./components/sector.jsx');
var Country = require('./components/country.jsx');
var Project = require('./components/project.jsx');


var routes = (
  React.createElement(Route, {name: "international", path: "/", handler: Root}, 
    React.createElement(DefaultRoute, {handler: International}), 
    React.createElement(Route, {name: "country", path: "/countries", handler: Country}), 
    React.createElement(Route, {name: "countrySector", path: "/sector/:sectorId", handler: Sector}), 
    React.createElement(Route, {name: "project", path: "/projects/:countryId/:projectId", handler: Project}), 
    React.createElement(Route, {name: "projectOnly", path: "/projects/:projectId", handler: Project})
  )
);


var router = Router.create({
  routes: routes,
  // location: Router.HistoryLocation  // <- uncomment to enable pushstate (no hash in url)
});
