'use strict';

var numeral = require('numeral');
var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;
var MapViewActions = require('../actions/mapViewActions');
var MetaActions = require('../actions/pageMetaActions');
var CountryActions = require('../actions/countryActions');
var CountrySummaryStore = require('../stores/countrySummaryStore');
var EsriTileLayer = require('./map/esriTileLayer.jsx');
// var PointLayer = require('./map/pointLayer.jsx');
// var Popup = require('./map/popup.jsx');
var Header = require('./header.jsx');

var BasemapsStore = require('../stores/basemapsStore');
var ThematicLayersStore = require('../stores/thematicLayersStore');
var Filters = require('./filters.jsx');


var Sector = React.createClass({displayName: "Sector",

  mixins: [Reflux.connect(CountrySummaryStore, 'countries'),
            Reflux.connect(BasemapsStore, 'basemaps'),
            Reflux.connect(ThematicLayersStore, 'thematicLayers')],

  getInitialState: function() {
    return { };
  },

  componentWillMount: function() {

    var _this = this;

    CountryActions.load.triggerPromise().then(function(featureCol) {
      var allCountriesList = featureCol.features.map(function(country) {
        return country.id;
      }, this);
      /* TODO(tdk): If selected sectors become > 1 then forward to Country route */
      /* TODO check all punctuations */

      MetaActions.transitionTo('country', {}, {c: allCountriesList, sectors: [_this.props.params.sectorId]});
      return this;
    }).then(MapViewActions.resetBounds);
    /* TODO(tdk): The international view set the bounds without padding, we should do it differently; */
    // {
    //   paddingTopLeft: [this.state.filtersWidth, 0],  // filters width and header height
    // }

  },

  filtersWidthChange: function(newWidth) {
    this.setState({filtersWidth: newWidth});
  },

  render: function() {
    // var countries = this.state.countries.features.sort(function(a, b) {
    //   return a.properties.name < b.properties.name ? -1 : 1;
    // });
    //
    return (
      React.createElement("div", null, 
        React.createElement(EsriTileLayer, {theme: "Streets", getMap: this.props.getMap}), 
        React.createElement(Header, null, 
          React.createElement(Header.Main, null, 
            React.createElement(Header.Item, null, 
              React.createElement("span", {className: "navbar-brand"}, this.props.params.sectorId, " Sector")
            )
          )
        ), 
        React.createElement(Filters, {className: "hidden", 
            notifyWidthChange: this.filtersWidthChange, 
            query: this.props.query})
      )
    );
  }

});


module.exports = Sector;
