'use strict';

var Reflux = require('reflux');
var CountryActions = require('../actions/countryActions');
var CountryStore = require('./countryStore');
var MapViewActions = require('../actions/mapViewActions');


var countrySummaryStore = Reflux.createStore({

  listenables: CountryActions,

  init: function() {
    this.state = {
      type: 'FeatureCollection',
      features: [],
    };
  },

  onLoadCompleted: function(data) {
    this.state = CountryStore.utils.sortCountries(data);
    this.trigger(this.state);
  },

  onLoadFailed: function() {
    console.error('booooooo loading countries failed');
  },

  getInitialState: function() {
    return this.state;
  },

});


module.exports = countrySummaryStore;
