'use strict';

var assign = require('object-assign');
var Reflux = require('reflux');
var api = require('../api');
var getRouter = require('../router.jsx').get;


var load = Reflux.createAction({ asyncResult: true });
load.listenAndPromise(api.getAllCountries);


var loadCountry = Reflux.createAction({ asyncResult: true });
loadCountry.listenAndPromise(api.getCountry);


var selectCountries = Reflux.createAction();


var addCountry = Reflux.createAction();
addCountry.listen(function(countryId) {
  var query = getRouter().getCurrentQuery(),
      countries = query.c.slice();  // copy, react-router gives us the original
  if (countries.indexOf(countryId) !== -1) {  // already in selection
    return;
  }
  countries.push(countryId);
  getRouter().transitionTo('country', {}, assign({}, query, {c: countries}));
});


var removeCountry = Reflux.createAction();
removeCountry.listen(function(countryId) {
  var query = getRouter().getCurrentQuery(),
      countries = query.c.slice(),  // copy, react-router gives us the original
      countryIndex = countries.indexOf(countryId);
  if (countryIndex === -1) {  // not in selection
    return;
  }
  countries.splice(query.c.indexOf(countryId), 1);  // delete mode
  getRouter().transitionTo('country', {}, assign({}, query, {c: countries}));
});


var clearCountries = Reflux.createAction();

module.exports = {
  load: load,
  loadCountry: loadCountry,
  selectCountries: selectCountries,
  addCountry: addCountry,
  removeCountry: removeCountry,
  clearCountries: clearCountries,
};
