'use strict';

var assign = require('object-assign');
var Reflux = require('reflux');
var CountryActions = require('../actions/countryActions');
var MapViewActions = require('../actions/mapViewActions');


function nestBoundsArray(a) {
  return [
    [a[1], a[0]],
    [a[3], a[2]],
  ];
}


function extractApprovalYear(location) {
  // parse out the year from the YYYY-MM-DD string (or null)
  var approvalDate = location.properties.approvalDate,
      approvalYear;
  if (approvalDate !== null) {
    approvalYear = approvalDate.split('-')[0];
  } else {
    approvalYear = null;
  }
  return assign({}, location, {
    properties: assign({}, location.properties, {
      approvalYear: approvalYear,
    }),
  });
}


function countryToFeature(rawCountry) {
  var feature = {
    type: 'Feature',
    id: rawCountry.id,
    geometry: {
      type: 'Point',
      coordinates: rawCountry.latlng
    },
    properties: assign({}, rawCountry.properties, {
      bounds: nestBoundsArray(rawCountry.properties.bounds),
      locations: rawCountry.features
        .map(extractApprovalYear),
    }),
  };
  feature.properties.locations.forEach(function(location) {
    location.properties.countryId = rawCountry.id;
    location.properties.countryName = rawCountry.properties.name;
  });
  return feature;
}

function getCountry(geojson, countryId) {
  var result;
  geojson.features.forEach(function(country) {
    if (country.id === countryId) { result = country; }
  });
  return result;
}


function removeCountry(state, countryId) {
  var country = getCountry(state, countryId);
  if (!country) { return assign({}, state); }
  var featuresWithout = [];
  state.features.forEach(function(someCountry) {
    if (someCountry !== country) {
      featuresWithout.push(someCountry);
    }
  });
  return assign({}, state, {features: featuresWithout});
}


function addCountry(state, countryFeature) {
  state = state || { type: 'FeatureCollection', features: [] };

  var features = state.features
    .filter(function(country) {  // remove the old copy of the country if present
      return country.id !== countryFeature.id;
    })
    .concat([countryFeature]);

  return assign(state, {features: features});
}


function sortCountries(state) {
  state = state || { type: 'FeatureCollection', features: [] };

  state.features = state.features
    .slice()
    .sort(function(countryA, countryB) {
      if (countryA.properties.name < countryB.properties.name) {
        return -1;
      } else if (countryA.properties.name > countryB.properties.name) {
        return 1;
      } else {
        return 0;
      }
    });

  return state;
}


var countryStore = Reflux.createStore({

  listenables: CountryActions,

  init: function() {
    this.state = {
      type: 'FeatureCollection',
      features: [],
    };
  },

  onLoadCountryCompleted: function(data) {
    this.state = sortCountries(addCountry(this.state, countryToFeature(data)));
    this.trigger(this.state);
  },

  onLoadCountryFailed: function() {
    console.error('could not load that country');
  },

  onClearCountries: function() {
    this.state = {
      type: 'FeatureCollection',
      features: [],
    };
  },

  getCountry: function(countryId) {
    return getCountry(this.state, countryId);
  },

  getInitialState: function() {
    return this.state;
  },

});


countryStore.utils = {
  nestBoundsArray: nestBoundsArray,
  countryToFeature: countryToFeature,
  getCountry: getCountry,
  removeCountry: removeCountry,
  addCountry: addCountry,
  sortCountries: sortCountries,
};


module.exports = countryStore;
