'use strict';

var assign = require('object-assign');
var Reflux = require('reflux');
var CountryStore = require('./countryStore');
var DGSet = require('../util').DGSet;


var approvalYearStore = Reflux.createStore({

  init: function() {
    this.data = new DGSet([]);
    this.listenTo(CountryStore, this.updateListFromCountries);
  },

  updateListFromCountries: function(countries) {
    this.data = new DGSet([]); // reset data store
    var year;
    var allYears = countries.features
      .filter(function(country) {
        return (country.properties.locations !== undefined);
      })
      .reduce(function(accum, country) {
        return accum.concat(country.properties.locations
          .reduce(function(years, location) {
            year = location.properties.approvalYear;
            if (year !== null) {
              years.push(year);
            }
            return years;
          }, []));
      }, []);
    this.data.update(allYears);
    // return object with counts of each year
    this.data.updateObject(allYears);
    this.emit();
  },

  emit: function() {
    this.trigger(this.data.asObject());
  },

  getInitialState: function() {
    return this.data.asArray();
  },

});


module.exports = approvalYearStore;
