'use strict';

var assign = require('object-assign');
var Reflux = require('reflux');
var MapViewActions = require('../actions/mapViewActions');
var getRouter = require('../router.jsx').get;


function clearSelected(basemap) {
  return assign({}, basemap, {selected: false});
}

function selectBasemap(basemaps, basemapId) {
  var foundIt = false;
  var updated = basemaps.map(function(basemap) {
    if (basemap.id === basemapId) {
      foundIt = true;
      return assign({}, basemap, {selected: true});
    } else {
      return basemap;
    }
  });
  if (!foundIt) {
    throw new Error('Could not find basemap to select.',
      'ID:', basemapId, 'basemaps', basemaps);
  }
  return updated;
}


var sectorStore = Reflux.createStore({

  init: function() {
    this.data = [
      {id: 'Streets', name: 'Streets', selected: true},
      {id: 'Gray', name: 'Gray', selected: false},
      {id: 'Topographic', name: 'Topographic', selected: false},
      {id: 'Imagery', name: 'Imagery', selected: false},
      {id: 'ImageryLabels', name: 'Imagery + Labels', selected: false},
      {id: 'Oceans', name: 'Oceans', selected: false},
      {id: 'NationalGeographic', name: 'National Geographic', selected: false},
      {id: 'OpenStreetMap', name: 'Open Street Map', selected: false},
    ];
    /** TODO: To support other arbitrary tile URLs basemaps in these properties,
     * in addition to the one needed for OpenStreetMap
     * change country.jsx's getBasemap to return obj instead of string
     * and read it from props.theme in esriTileLayer.jsx
     **/
    this.listenTo(MapViewActions.refreshFromURL, this.onRefreshFromURL);
  },

  onRefreshFromURL: function() {
    var query = getRouter().getCurrentQuery();
    var updated = this.data.map(clearSelected);
    if (query.bm !== undefined) {
      this.data = selectBasemap(updated, query.bm);
    } else {
      this.data = selectBasemap(updated, 'Streets');
    }
    this.emit();
  },

  emit: function() {
    this.trigger(this.data);
  },

  getInitialState: function() {
    return this.data;
  },

});


module.exports = sectorStore;
