/**
 * Location Pin
 *
 * This is _not_ actually a react component, it returns a leaflet layer,
 * intended for use as a callback. It's just javascript, not jsx, and
 * it exports a simple function.
 */
'use strict';

var L = window.L;  // explicitly bring into scope to not be evil
var api = require('../api.js');


function locationPin(location, latlng) {
  var size = 16,
      sector;
  var apiBaseUrl = api.getAPIBase();

  if (location.properties.sectors.length > 1) {
    sector = 'Multisector';
  } else if (location.properties.sectors.length !== 1) {
    console.error('no sector for location', location);
    sector = 'Unknown';
  } else {
    sector = location.properties.sectors[0];
  }

  return L.marker(latlng, {
    icon: L.divIcon({
      html: '<img class="marker-only-image" src="' + apiBaseUrl + 'images/sector/' + sector + '.svg" alt="Sector: ' + sector + '" />',
      className: 'project-location-marker',
      iconSize: [size, size],
      iconAnchor: [size/2, size/2],
    }),
    riseOnHover: true,
  });
}

module.exports = locationPin;
