'use strict';

var Reflux = require('reflux');
var countryActions = require('../actions/countryActions');
var countryStore = require('./countryStore');


var selectedCountryStore = Reflux.createStore({

  init: function() {
    this.listenTo(countryStore, this.countriesUpdated, this.countriesInit);
    this.listenTo(countryActions.selectCountries, this.onSelectCountries);
  },

  countriesInit: function(initialCountries) {
    this.state = initialCountries;
  },

  countriesUpdated: function(newCountries) {
    this.state = newCountries;
    this.emit();
  },

  onSelectCountries: function(countryIds) {
    this._selected = countryIds;
    this.emit();
  },

  get: function() {
    var countries;

    if (this._selected !== undefined) {
      countries = this.state.features.filter(function(country) {
        return (this._selected.indexOf(country.id) !== -1);
      }, this);
    } else {
      countries = [];
    }

    return {
      type: 'FeatureCollection',
      features: countries,
    };
  },

  emit: function() {
    this.trigger(this.get());
  },

  getInitialState: function() {
    return this.state;
  },

});


module.exports = selectedCountryStore;
