'use strict';

var React = require('react');
var Reflux = require('reflux');
var Popup = require('./map/popup.jsx');
var ErrorStore = require('../stores/systemErrorStore');

var ErrorMessage = React.createClass({displayName: "ErrorMessage",
  getInitialState: function() {
    return {init: true, closed: true, filterClicked: false};
  },
  componentDidMount: function() {
    ErrorStore.listen(this.checkStatus);
  },
  clickClose: function(e) {
    if(e) { e.preventDefault(); }
    this.setState({closed: true});
  },
  checkStatus: function(data) {
    if(data.filterClicked === true && this.state.init === true && this.state.closed === false) {
      //the very first time a filter is clicked, all flags are reset to eliminate any noise from page loads
      this.setState({init: false, filterClicked: false, closed: true});
    }
    else {
      this.setState(data);
    }
  },
  render: function() {
    var showError = this.state.filterClicked && !this.state.closed;
    var divClasses = "filters-error " + (showError === false ? 'hidden' : '');
    return (
      React.createElement("div", {className: divClasses, ref: "error"}, 
        React.createElement(Popup, {className: "popup-error"}, 
          React.createElement(Popup.Header, null, 
            React.createElement("h3", {className: "panel-title"}, 
              React.createElement("strong", null, this.props.header)
            ), 
            React.createElement("a", {href: "#", className: "close-error pull-right", onClick: this.clickClose}, "  ")
          ), 
          React.createElement("div", {className: "panel-body"}, 
            this.props.text
          )
        )
      )
    )
  },
});

module.exports = ErrorMessage;
