var React = require('react');


var Popup = React.createClass({displayName: "Popup",
  render: function() {
    return (
      React.createElement("div", {className: 'panel panel-default ' + this.props.className || ''}, 
        this.props.children
      )
    );
  },
});


Popup.Header = React.createClass({displayName: "Header",
  render: function() {
    return (
      React.createElement("div", {className: "panel-heading popup-header"}, 
        this.props.children
      )
    );
  },
});


module.exports = Popup;
