'use strict';

var Reflux = require('reflux');
var LocationsStore = require('./locationsStore');
var FilterStore = require('./filterStore');


var LocationsStore = Reflux.createStore({

  init: function() {
    this.data = [];
    this._filter = null;
    this.listenTo(LocationsStore, this.updateLocations);
    this.listenTo(FilterStore, this.updateFilter, this.initFilter);
  },

  updateLocations: function(newLocations) {
    this.data = newLocations;
    this.emit();
  },

  initFilter: function(initialFilter) {
    this.setFilter(initialFilter, true);
  },

  updateFilter: function(newFilter) {
    this.setFilter(newFilter);
  },

  setFilter: function(newFilter, quiet) {
    this._filter = newFilter;
    !quiet && this.emit();
  },

  get: function() {
    var _this = this,
        filteredLocations = this.data;

    if (this._filter.sectors &&
        this._filter.sectors.length > 0) {
      var locationsMatchingSector = this.data.filter(function(location) {
        return (location.properties !== undefined) &&
          location.properties.sectors.some(function(sector) {
            return _this._filter.sectors.indexOf(sector) > -1;
          });
      });
      filteredLocations = filteredLocations.filter(function(location) {
        return locationsMatchingSector.indexOf(location) > -1;
      });
    }

    if (this._filter.status &&
        this._filter.status.length > 0) {
      var locationsMatchingStatus = this.data.filter(function(location) {
        return (location.properties !== undefined) &&
            _this._filter.status.indexOf(location.properties.status) > -1;
      });
      filteredLocations = filteredLocations.filter(function(location) {
        return locationsMatchingStatus.indexOf(location) > -1;
      });
    }

    if (this._filter.approvalYear &&
        this._filter.approvalYear.length > 0) {
      var locationsMatchingYear = this.data.filter(function(location) {
        if (location.properties !== undefined) {
          var year = location.properties.approvalYear;
          return _this._filter.approvalYear.indexOf(year) > -1;
        } else {
          return false;
        }
      });
      filteredLocations = filteredLocations.filter(function(location) {
        return locationsMatchingYear.indexOf(location) > -1;
      });
    }

    return filteredLocations;
  },

  emit: function() {
    this.trigger(this.get());
  },

  getInitialState: function() {
    return this.get();
  },

});


module.exports = LocationsStore;
