'use strict';

var _reqw = require('reqwest');
var Q = require('q');

var constAPIBase = 'http://adb.dgstg.org/1dfd37/';

var debugMode = (window.location.host === '127.0.0.1:9010');
if (debugMode) {
  var constAPIBase = '';
}

function reqP(/* arguments */) {
  // wrap reqwest's non-compliant promise with a promise from Q that actually works
  return Q.when(_reqw.apply(null, arguments))
    .then(function(data) {
      if (data instanceof XMLHttpRequest) {
        data = JSON.parse(data.responseText);
      }

      return data;
    })
    .fail(function(err, message) {
      console.error(message, err);
    });
}

module.exports = {

  getThematicLayerLegend: function(layerId) {
    /* URL of this should be absolute */
    return reqP({url: layerId.url + '/legend?f=json', crossOrigin: true, contentType: 'text/plain', type: 'json'});
  },

  getAllCountries: function() {
    return reqP({ url: constAPIBase + 'json/countries.json' , crossOrigin: true, contentType: 'text/plain', type: 'json'});
  },

  getCountry: function(countryId) {
    return reqP({ url: constAPIBase + 'json/countries/' + countryId + '.json', crossOrigin: true, contentType: 'text/plain', type: 'json'});
  },

  getProject: function(projectId) {
    return reqP({ url: constAPIBase + 'json/projects/' + projectId + '.json', crossOrigin: true, contentType: 'text/plain', type: 'json'});
  },

  getAPIBase: function() {
    return constAPIBase;
  }

};
