/*
 * Leaflet Layer Mixin
 *
 * Handles lifecycle transitions for layers that need to talk to leaflet.
 */
'use strict';
var objectEq = require('../../util').objectEq;


var LReactMapLayerMixin = {

  componentDidMount: function() {
    this.addLayerToMap(this.props);

    /* ASDB-191 Blank Basemaps */
    var classNameArray = this.props.getMap().getContainer().className.split(' ');
    if (this.props.getMap() && classNameArray.indexOf('basemap-box') > 0) {
      this.props.getMap().setZoom(0);
    }
  },

  componentDidUpdate: function(prevProps, prevState) {
    if (!objectEq(prevProps, this.props)) {
      this.addLayerToMap(this.props);
    }
  },

  componentWillUnmount: function() {
    var map;

    try {
      map = this.props.getMap();
    } catch (e) {
      console.warn('could not get map to remove layer', e);
      return;
    }

    if (this.layer) {
      map.removeLayer(this.layer);
    } // else {} ... is there something we need to handle here???
  },
};


module.exports = LReactMapLayerMixin;
